/* Apply the "Archivo" font family to HTML and body elements */
html,
body {
    font-family: "Archivo", sans-serif;
    scroll-behavior: smooth;
    /* Enable smooth scrolling behavior */
}

/* Style for the main scrollbar */
body::-webkit-scrollbar {
    cursor: pointer;
    /* Change cursor on hover */
    width: 0.4em;
    /* Set the width of the scrollbar */
    border-radius: 50vh;
    /* Rounded scrollbar shape */
}

/* Keyframes animation for moving from right to left */
@keyframes leftanimation {
    0% {
        transform: translateX(100%);
        /* Start position, fully to the right */
    }

    100% {
        transform: translateX(0);
        /* End position, at the original position */
    }
}

/* CSS class to apply the 'leftanimation' animation */
.leftanimation {
    animation: leftanimation 1s ease-in-out;
    /* Apply the animation */
    animation-fill-mode: forwards;
    /* Keep the final state after animation */
}

/* Keyframes animation for moving from bottom to top */
@keyframes topanimation {
    0% {
        transform: translateY(100%);
        /* Start position, fully at the bottom */
    }

    100% {
        transform: translateY(0);
        /* End position, at the original position */
    }
}

/* CSS class to apply the 'topanimation' animation */
.topanimation {
    animation: topanimation 1s ease-in-out forwards;
    /* Apply the animation and keep final state */
}

/* Keyframes animation for moving from top to bottom */
@keyframes downanimation {
    0% {
        transform: translateY(-100%);
    }

    100% {
        transform: translateY(0);
    }
}

/* CSS class to apply the 'downanimation' animation */
.downanimation {
    animation: downanimation 1s ease-in-out forwards;
}

/* Keyframes animation for fast moving from top to bottom */
@keyframes fastupanimation {
    0% {
        transform: translateY(100%);
    }

    100% {
        transform: translateY(0);
    }
}

/* CSS class to apply the 'fastupanimation' animation */
.fastupanimation {
    animation: fastupanimation 0.4s ease-in-out forwards;
}

/* Keyframes animation for fast moving from bottom to top */
@keyframes fastdownanimation {
    0% {
        transform: translateY(-100%);
    }

    100% {
        transform: translateY(0);
    }
}

/* CSS class to apply the 'fastdownanimation' animation */
.fastdownanimation {
    animation: fastdownanimation 0.4s ease-in-out forwards;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.hidescrollbar::-webkit-scrollbar {
    display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.hidescrollbar {
    -ms-overflow-style: none;
    /* IE and Edge */
    scrollbar-width: none;
    /* Firefox */
}